

.game-field {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: #000000;
}

.main-preview {
  display: block;
  margin: 0 auto;
  padding-top: 96px;
}

.game1-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 668px;
  height: 870px;
  margin: 0 auto;
  position: relative;
  &.final-container {
    justify-content: flex-start;
  }
  & .container-title {
    font-family: 'NeueMachina-Regular';
    font-size: 50px;
    line-height: 56px;
    &.question-title {
      font-size: 30px;
      line-height: 38px;
    }
  }
  & .container-content {
    & img {
      display: block;
      width: auto;
      max-height: 350px;
      margin: 0 auto;
      margin-top: 20px;
    }
    & p {
      font-size: 20px;
      line-height: 30px;
      &.question-content {
        font-size: 18px;
        line-height: 28px;
      }
    }
    .result-title {
      font-size: 30px;
      line-height: 38px;
      margin-bottom: 30px;
    }
    .results {
      & > div {
        font-size: 18px;
        line-height: 28px;
        margin-bottom: 20px;
      }
      & > p {
        font-size: 18px;
        line-height: 28px;
        margin: 0;
        margin-bottom: 40px;
      }
    }
    & .cup-block {
      display: flex;
      align-items: flex-end;
      margin-top: 155px;
      margin-bottom: 50px;
      & img {
        margin: 0;
        margin-right: 30px;
      }
      & .total-score {
        position: relative;
        & > div {
          display: flex;
          align-items: flex-end;
          font-family: 'my_phone_n1280', sans-serif;
          font-size: 100px;
          line-height: 75px;
          color: #FAFF03;
          & span {
            position: absolute;
            top: -30px;
            font-size: 16px;
            line-height: 24px;
            color: #FAFF03;
          }
          & p {
            font-size: 30px;
            line-height: 22px;
            color: #FAFF03;
            margin: 0;
          }
        }
      }
    }
  }
  & .start-btn {
    color: #FF1BF7;
    background: url("/images/game1/default-btn.svg")no-repeat center / cover;
  }
}

.back-arrow {
  padding-top: 30px;
  &.nav-panel {
    display: flex;
    font-size: 22px;
    line-height: 16px;
    align-items: center;
    justify-content: space-between;
    & .points {
      display: flex;
      align-items: center;
      & img {
        margin-right: 10px;
      }
    }
  }
}

.true-false-btn {
  display: flex;
  & > div {
    color: #000000;
  }
}
.true-btn {
  background: url("/images/game1/true-btn.svg")no-repeat center / cover;
}
.false-btn {
  background: url("/images/game1/false-btn.svg")no-repeat center / cover;
}
.next-btn {
  background: url("/images/game1/black-btn.svg")no-repeat center / cover;
}


.hover-scale {
  transform: scale(1);
  transition: .4s;
  cursor: pointer;
  &:hover {
    transform: scale(1.2);
  }
}


//==================**Status popup styles**=================

.status-popup {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,.8);
  & .status-popup-wrapper {
    display: flex;
    width: 355px;
    //height: auto;
    //min-height: 465px;
  }
  &__content {
    padding: 40px 30px;
    position: relative;
    & .title {
      font-family: 'NeueMachina-Regular';
      font-size: 30px;
      line-height: 34px;
      color: #000000;
      margin-bottom: 20px;
    }
    & p {
      font-family: 'Circe-Regular';
      font-size: 18px;
      line-height: 26px;
      color: #000000;
    }
    & .btn-wrapper {

      & > div {
        color: #000000;
      }
    }
  }
  &.fake-status {
    & .status-popup__content {
      background: #FF3303;
    }
  }
  &.really-status {
    & .status-popup__content {
      background: #02FF39;
    }
  }
}
